.confirmation {
  margin: 150px auto 0;
  text-align: center;
}
.confirmation .orderInfo {
  display: flex;
  justify-content: space-around;
  gap: 2rem;
  text-align: left;
}

@media screen and (max-width: 600px) {
  .confirmation .orderInfo {
    flex-direction: column;
    gap: 1rem;
  }
}
