/* custom select element styling */
/*  Remove default styling of select element  */
*::before,
*::after {
  box-sizing: border-box;
}
select {
  -webkit-appearance: none;
          appearance: none;
  background-color: transparent;
  border: none;
  padding: 0 1rem 0 1rem;
  margin: 0;
  /* width: 100%; */
  height: 32px;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  outline: none;
  color: #463a3b;
}
/*   New styling to Select  */
.customSelect {
  /* min-width: 15ch; */
  border-radius: 0.25em;
  /* width: 100%; */
  cursor: pointer;
  background-color: #fff;
  border: solid #b3c5ce 1px;
  display: grid;
  grid-template-areas: "select";
  -webkit-align-items: center;
          align-items: center;
  position: relative;
}

/*  Custom select-arrow  */
.customSelect::after {
  content: "";
  width: 0.7em;
  height: 0.4em;
  background-color: #463a3b;
  -webkit-clip-path: polygon(100% 0%, 0 0%, 50% 100%);
          clip-path: polygon(100% 0%, 0 0%, 50% 100%);
  justify-self: end;
  margin-right: 1rem;
}

select,
.customSelect:after {
  grid-area: select;
}

/*  Select focus state  */
select:focus + .focus {
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  border: 2px solid rgb(141, 157, 165);
  border-radius: inherit;
}

@media screen and (max-width: 600px) {
  .pages-container {
    padding: 0 30px;
  }
  button {
    font-size: 1.1rem;
  }
  .btn-sm {
    font-size: 0.8rem;
  }
}

